import {Injectable} from '@angular/core'
import { BehaviorSubject } from 'rxjs';

@Injectable()

export class productCardDTO{
    private productdto:BehaviorSubject<boolean>=new BehaviorSubject(false);
    private cartdto:BehaviorSubject<Array<any>>=new BehaviorSubject([]);
        constructor(){
    }

    returncartdto(){
      return this.cartdto;
    }
    returncart(){
        return this.productdto;
    }

    setCartArray(array){
      this.cartdto.next(array);
    }

  setCartStatus(value:boolean){
    this.productdto.next(value);
  }

  
}
