import { Component, OnInit } from '@angular/core';
import { FillDataService } from '../../moduleServices/addService';
import { OrderService } from '../../moduleServices/orderService';
import {OrderModel,GiftMessage} from '../../../../globalmodel/sendorder.model'
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { productCardDTO } from '../../moduleServices/productCartDto';

@Component({
  selector: 'app-checkoutpage',
  templateUrl: './checkoutpage.component.html',
  styleUrls: ['./checkoutpage.component.css'],
  providers:[FillDataService,OrderService]
})
export class CheckoutpageComponent implements OnInit {
private step:number=-1;
private cartArray:any[]=[];
private transId:string=null;
private firstName:String=null;
private addressArray=[];
private timeSlot=null;
private order;
private isShow:Boolean=false;
private giftMessage=null;
private fulladdress:string=null;
private totalAmtPayee:number=null;
  constructor(private filldataobj:FillDataService,
    private router:Router,private orderCrud:OrderService,private snackBar:MatSnackBar,
    private cartdto:productCardDTO) { }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

setTimeSlot(data){ 
  console.log(data); //set TimeSlot  
this.timeSlot=data;
}
setTransId(data){
  if(data!=null){  //Trans Id Submitted
this.transId=data;
this.proceedToPay();
  }else{
    this.openSnackBar('Some Error Ocurred','')
  }
}

checkOrderTimeSlotDetails(){

if(this.timeSlot!=null){
  for(let key in this.timeSlot){
    if(key!='type' && this.timeSlot[key]!=null){
   for(let innerkey in this.timeSlot[key]){
let innerdata= this.timeSlot[key][innerkey];
if(innerdata==null){
  return null;
}
   }
  }else if(this.timeSlot[key]==null){
return null;
  }

}

return this.timeSlot;
}
return null;
}

isOrderNull(orderobj){
  for(let key in orderobj){
 if(key!='giftMessage'){
   if(orderobj[key]==null){
return true;
   }
 }
  }
  return false;
}

setOrdergiftMessage(){
  if(this.giftMessage!=null){
let giftmess:GiftMessage= new GiftMessage();
giftmess.senderName=this.giftMessage.senderName;
giftmess.recieverName=this.giftMessage.recieverName;
giftmess.message=this.giftMessage.message;
return giftmess;
  }
  return null;
}

backendPlace(orderobj){
  this.orderCrud.placeOrder({'order':orderobj}).subscribe(data=>{
    if(data.isPushed==true){
      this.cartdto.setCartStatus(true);
   this.router.navigate(['/customer/orderStatus',data.order.orderId]);
    }
  },err=>{
    if(err.message!=null){
    this.openSnackBar(err.message,'')
    }else{
      this.openSnackBar(err,'');
    }
  })
}


placeOrder(){
  let orderobj= new OrderModel();
  orderobj.timeAndTypeSlot=this.checkOrderTimeSlotDetails();
orderobj.giftMessage=this.setOrdergiftMessage();
orderobj.payment=this.totalAmtPayee;
orderobj.paymentMethod='COD';
orderobj.transactionId=this.transId;
console.log(orderobj);
if(this.isOrderNull(orderobj)==false){
  this.backendPlace(orderobj);
}
}
proceedToPay(){
  this.placeOrder();
}



  ngOnInit() {
    this.getBackendDelivData();  
  }
  getIsAlreadyPresent(){    //if address is already present
    return this.isShow;
 }
 setgiftMessage(data:any){
   if(data!=null){
this.giftMessage=data;
//console.log(this.giftMessage);
   }
   this.setStep(2);
 }

setAmt(data:number){
  if(data!=null){
this.totalAmtPayee=data;         //set amount to be paid
}
}

setPaymentAllowed(){
  let status=false
  if(this.isShow==true && this.totalAmtPayee!=null){
    status=true
  }
  return status;
}

 setShowStatus(data){     //set the status show card or form of Address
   this.isShow=data;
 }
 changeAddress(){
   this.isShow=false;    //show cust add or deliver add
 }
setCallBack(data){        //after address is filled and send get new address
  if(data==true){
    this.getBackendDelivData();
    
  }
}

setAsAdd(data){     //set new current Address
this.filldataobj.setCurrentAddress({'addId':data.addId}).subscribe(data=>{
  if(data.status=='S' && data.isEdited==true){
    this.setStep(2);
  }else{
    this.openSnackBar('Error Ocurred','');
  }
},err=>{
  if(err.message!=null){
  this.openSnackBar(err.message,'');}
  else{
    this.openSnackBar(err,'');
  }
})
}
proceedPay(){   //go to Payment Section 
  this.setStep(3);
}

setCartArray(data){  //set Cart Data
  this.cartArray=data;
}

  getBackendDelivData(){  //get Registered Address
    this.filldataobj.getDelivAddress().subscribe(data=>{
        if(data.status=="S"){
        // console.log(data);
          if(data.userdetails.addressArray.length!=0){
          this.addressArray=[];
          
          this.firstName=data.userdetails.firstName;
          this.addressArray=data.userdetails.addressArray;
         this.fulladdress=data.userdetails.addressArray[0].fulladdress;
          this.isShow=true;
          if(data.userdetails.defaultAddId!=null){
        
            this.setStep(2);
          }else{
        this.setStep(0);
          }}else{
            this.isShow=false;
            this.setStep(0);
          }
          
      }else{
     this.isShow=false;
    this.setStep(0); 
    }

    },err=>{
this.isShow=false;
this.setStep(0);
    })
  }
  setStep(index:number){
    this.step=index;
  }
}


