import { Component, OnInit } from '@angular/core';
import { AbtcrudService } from '../../adminModule/AboutCrud/service/abtcrud';

@Component({
    selector:'footer',
    templateUrl:'./footer.component.html',
    styleUrls:['./footer.component.css'],
    providers:[AbtcrudService]
})

export class FooterComponent implements OnInit{
    private readMore=false;
    private aboutData=null;
    private footerNav:any[]=[];
    constructor(private abtservice:AbtcrudService){
       
    }
    ngOnInit(){
        this.getInitialData();
    }

    filterData(data){
        this.footerNav=[];
  for(let titleitem of data.data.titleFooter){
      let listitemArray=[];
for(let item of titleitem.titleList){

    let listitem ={
        listName:item.listName,
        listLink:item.listLink
    }
    listitemArray.push(listitem);
}

let footerdata={
    titleName:titleitem.titleName,
    titleList:listitemArray
}
this.footerNav.push(footerdata);

  }
    }

    getInitialData() {
        this.abtservice.Aboutjsonretrieve().subscribe(data=>{
            //console.log(data.data);
            this.aboutData=data.data;
        }) 
        
        this.abtservice.getData().subscribe(data=>{
            if(data.status=='S'){
                this.filterData(data);
               console.log(this.footerNav);
            }else{
                this.footerNav=[];
            }

        },err=>{
            this.footerNav=[];
        })
    }

    readmore(){
        this.readMore=true;
    }
}