import {Injectable} from '@angular/core';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import { Observable,throwError } from 'rxjs';
//import { httpOptions } from "../../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import { NgForm } from '@angular/forms';
import {url} from '../../../../globalmodel/url'
import { abtcrudModel } from '../model/abtcrudModel';
@Injectable()

export class AbtcrudService{
    private pushUrl=url.pushUrl
    private getUrl=url.getUrl;
    private getNavUrl= url.getFooterNav;
    constructor(private http:HttpClient){
    }
    handleError(error:HttpErrorResponse){
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else {
         err = 'BackendError'
             }
      
              return throwError(
          err);
             };
    uploadForm(data:NgForm):Observable<any>{
        
   return this.http.post<any>(this.pushUrl,data).pipe(
   catchError(this.handleError));    
   };
   Aboutjsonretrieve(){    
    return this.http.get<any>(this.getUrl);
   }
   getData():Observable<any>{
    return this.http.get<any>(this.getNavUrl).pipe(
         catchError(this.handleError));   
    }
    

}