import { Component, OnInit, Input } from '@angular/core';


@Component({
  selector: 'app-loopcarousel',
  templateUrl: './loopcarousel.component.html',
  styleUrls: ['./loopcarousel.component.css']
})
export class LoopcarouselComponent implements OnInit {
  private activeIndex:number=0;
  private MAXTABS:number=0;
  private slideConfig;
  @Input() config;
  @Input() showcontentHeader='true';


  @Input('carouselData') slides;

   ngOnInit(){
    this.slideConfig = this.config;
     if(this.slides!=null){
     this.MAXTABS=this.slides.length;
     console.log(this.MAXTABS);
   }}
  
  beforeChange(e){
  
  this.activeIndex=e.nextSlide;  
  }




 

}
