import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-productcategory',
  templateUrl: './productcategory.component.html',
  styleUrls: ['./productcategory.component.css']
})
export class ProductcategoryComponent implements OnInit {
@Input() product=null;
@Input() isCard:boolean=true
private queryparams:any={};

  constructor() { }
setlink(){
  this.queryparams.link=this.product.link;
}
setlabel(){
  this.queryparams.label= this.product.label;
}

setcategory(){
  this.queryparams.category=this.product.params[0].category;
}

setParams(){
  this.product.params.forEach((paramobj)=>{
   this.queryparams[paramobj.keyword]=paramobj.value;
  })
}

  ngOnInit() {
    console.log(this.product);
    if(this.product!=null){
   this.setlink();
   this.setcategory();
       this.setParams();
       this.setlabel();
    }
  

}
}