import { Component, OnInit,Input, Output, EventEmitter } from '@angular/core';
import { VoucherCrud } from './services/voucherCrud';


@Component({
  selector: 'cust-voucher-data',
  templateUrl: './voucher-data.component.html',
  styleUrls: ['./voucher-data.component.css'],
  providers:[VoucherCrud]
})
export class VoucherDataComponent implements OnInit {
@Input() voucher=null;
@Output() selected=new EventEmitter();
private templateData:string=null;
private displayTC:boolean=null;
@Input() index:number=null;
  constructor(private voucherService:VoucherCrud) { }

  ngOnInit() {
 
  }

  selectThis(voucherCode){
this.selected.emit({code:voucherCode,pos:this.index});
  }

  showTermCond(){
    if(this.templateData==null){
      this.voucherService.getTemplateData({templateId:this.voucher.templateId}).subscribe(data=>{
        console.log(data);
        if(data.status=='S'){
this.templateData=data.data.templateData;
        }else{
          alert('Unknown Error');
        }
      },err=>{
        if(err.message!=null){
        alert(err.message);}
        else{
          alert(err);
        }
      })
    }
this.displayTC=!this.displayTC;
  }
}
