import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import {Ads} from '../globalmodel/ad/ads.modal'
import { comments } from '../globalmodel/comments';

@Injectable()
export class getAds{
url = url.localgetAds;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
       console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=0){
         err = error.error;
             }else{
                 err=comments.networkError;
             }
      
              return throwError(
          err);
             };

getAds():Observable<Ads[]>{
       return this.http.get<Ads[]>(this.url,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

        
    }
}