import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { SearchService } from './services/searchService';

@Component({
  selector: 'app-sp',
  templateUrl: './sp.component.html',
  styleUrls: ['./sp.component.css'],
  providers:[SearchService]
})
export class SpComponent implements OnInit {
  private label:string;
private category:string;
private searchUrl:string;
private paramsendobj:any={};
private selectedsubCat:string;

private selectedsubproducts:any=[];
private subproductarray=[];
constructor(private route:ActivatedRoute,private searchservice:SearchService) { }

setreqparams(params){
 (<Array<string>>params.keys).filter(key=>{
    return key!='category' && key !='link' && key!='label';
 }).forEach(keyvalue=>{
   this.paramsendobj[keyvalue]=params.get(keyvalue);
 })

}

  ngOnInit() {
    this.route.queryParamMap.subscribe(params=>{
if(params!=null){
this.category=params.get('category');
this.searchUrl= params.get('link');
this.label = params.get('label');
this.setreqparams(params);
if(this.paramsendobj!=null && this.category!=null && this.searchUrl!=null){
this.searchservice.searchquery(this.searchUrl,{ 'categoryName':this.category,
'subcategoryName':this.label ,'params':this.paramsendobj}).subscribe(data=>{
  if(data.status=='S'){
   // console.log(data.products);
    this.selectedsubCat=data.products.subcategoryName;
    this.subproductarray=data.products;
  this.createsubproductlist();
  }
},err=>{
  if(err.message!=null){
alert(err.message);
  }else{
    alert(err);
  }
 
})

}}
    })
  }


  createsubproductlist(){
   
  
    
    for(let subproduct of this.subproductarray){
      let stackTrace=[];
      stackTrace.push(subproduct.categoryId);
      stackTrace.push(subproduct.subcategoryId);
      stackTrace.push(subproduct.productId)
      stackTrace.push(subproduct.subproductId)
         this.selectedsubproducts.push({ 'stackTrace':stackTrace ,'subproduct':subproduct});
  }
  console.log(this.subproductarray);
  
  }


}
