import { Injectable  } from "@angular/core";
import { HttpClient,HttpBackend } from '@angular/common/http';
import {category} from "../globalmodel/listitem/category";
import { httpOptions } from '../globalmodel/headerDetails';
import { url } from '../globalmodel/url'


@Injectable()

export class CategoryJsonretriever{
   private url =url.localgetCategorylist;
   private http:HttpClient;
    constructor(handler: HttpBackend){
     this.http = new HttpClient(handler);
    }

    jsonretrieve(){
        
     return this.http.get<category[]>(this.url,httpOptions.jsonHeaders);
     
    }
}