import {Pipe,PipeTransform} from '@angular/core';


@Pipe({name:'activePipe'})
export class activePipe implements PipeTransform{


transform(adArray:any[],type:string){
    
    if(!adArray || !type){
return adArray;
    }
else if(type=='active'){
  
    let subadarray=[]; 

    subadarray= adArray.filter(element=>{
          return element.status=='active'
      })
      console.log(subadarray);
  
  
   return subadarray;
}
}}
