import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import {cartProduct } from 'src/app/globalmodel/cartProduct';
import { productCardDTO } from '../moduleServices/productCartDto';


@Component({
  selector: 'product-card',
  templateUrl: './product-card.component.html',
  styleUrls: ['./product-card.component.css']
})
export class ProductCardComponent implements OnInit,OnDestroy {
@Input('productObj') product;
@Input() stackTrace;
private amountDisable:boolean=false;
private selectedAmount:any=null;
private cartproduct:cartProduct;
private currentIndex:number=0;
private nullcartobj:any={
cartProductId:null,
quantity:0
};

private selectedcartlist:any[]=[];
  public discount:string=null;
  public sellprice:string=null
constructor(private cartdto:productCardDTO) { 
 
  
}

selectchange(amount:any,cartproductid,quantity){  //select the new amount and new cart product to be added
  this.currentIndex=(<Array<any>>(this.product.info.priceAndAmount)).findIndex(item=> item.amount==amount.amount);  //find the currentIndex for value change
  if(this.selectedcartlist.length!=0){
   
  for(let cartItem of this.selectedcartlist){
    //console.log(amount);
    if(amount.amount==cartItem.amount && amount.suffix==cartItem.suffix){
     // console.log('here',cartItem)
      cartproductid=cartItem.cartProductId;
      quantity=cartItem.quantity;
    }
  }
  }
  //console.log(amount,cartproductid,quantity);
  this.discount= parseFloat(amount.discount).toString();
  this.sellprice=((parseFloat(amount.price)-(0.01*parseInt(amount.discount)*parseFloat(amount.price))).toFixed(2));
  this.setsubproductdata(amount,cartproductid,quantity);
}

setpriceamount(cartproduct,index){
  if(this.product.info.priceAndAmount[index]!=null && this.product.info.priceAndAmount[index].discount!=null ){
    if(cartproduct!=null){
      this.selectchange(this.product.info.priceAndAmount[index],cartproduct.cartProductId,cartproduct.quantity);  //set the price amount shown and select the cart product
    }else{
      this.selectchange(this.product.info.priceAndAmount[index],null,0);
  
    }
      this.selectedAmount= this.product.info.priceAndAmount[index];
  }
  
}

  ngOnInit() {

 if(this.product!=null){

  this.cartdto.returncartdto().subscribe(array=>{  //as new cart product comes it refreshes the new cartproduct
  // console.log('here',array);
    if(array.length!=0 && array[0]!='Empty'){
      this.selectedcartlist=[];
      this.currentIndex=0;
    for(let product of array){
  
      if(product.subproductId==this.product.subproductId){


          for(let i=0;i< this.product.info.priceAndAmount.length;i++){
            let value=this.product.info.priceAndAmount[i];
            if(product.amount==value.amount && product.suffix==value.suffix){
         
            this.currentIndex=i;
         
         this.selectedcartlist.push(product);       //list of those who have aleady been added to cart

               
            }
          }
      }
    }
    if(this.selectedcartlist.length!=0){        
      if(this.selectedAmount!=null){      //if already previous selected amount was there so open that and have that cartProduct ready
      this.currentIndex=(<Array<any>>(this.product.info.priceAndAmount)).findIndex(item=> item.amount==this.selectedAmount.amount);
      }
     // console.log(this.selectedcartlist,this.currentIndex);
    this.setpriceamount(this.selectedcartlist[this.currentIndex],this.currentIndex);
    }else{
      this.setpriceamount(this.nullcartobj,0);
    }
  }else{
   
    this.setpriceamount(this.nullcartobj,0);
  }})}

  
}


  setsubproductdata(amount,cartproductid,quantity){
   // console.log(quantity);
    this.cartproduct= new cartProduct();  
    this.cartproduct.Qua=quantity;
     //set the cartProduct to send to the database
    this.cartproduct.CartPId=cartproductid;
    this.cartproduct.Amt= parseFloat(amount.amount);
    this.cartproduct.Suffix=amount.suffix;
   this.cartproduct.SubPId=this.product.subproductId;
   this.cartproduct.SubPName=this.product.subproductName;
  this.setAmtOption(quantity);
   //console.log(this.cartproduct);
   
  }


  setAmtOption(data:number){          //Event emitter to disable the amount selection once added to the cart
   // console.log('im here',data)
if(data!=0){
  this.amountDisable=true;
  
}else{
  this.amountDisable=false;
  
}

  }

  
ngOnDestroy(){
 // this.cartdto.returncartdto().unsubscribe();
}
  
}

