import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-time-line',
  templateUrl: './time-line.component.html',
  styleUrls: ['./time-line.component.css']
})
export class TimeLineComponent implements OnInit {
@Input() set Levels(val){
this.levels=val;
//console.log(val);
}
@Input() set CurrentLvl(val){
this.currentLevel=val;
//console.log(this.currentLevel);
}

private percentageValue=0;
private currentLevel=0;
private levels=null;
  constructor() { }

  ngOnInit() {
    this.computePercentage();
  }

  computePercentage(){
      let perPercent = (100/(this.levels.length-1));
   this.percentageValue=perPercent*this.currentLevel;   
   //console.log(this.percentageValue);
  }

}
