import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, NgForm } from '@angular/forms';
import { getErrorMessage } from 'src/app/globalServices/Validation';
import { AuthenticationService } from '../login-signup/services/authenticateUser';

@Component({
  selector: 'cust-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class CustomerLoginComponent implements OnInit {
private userForm:FormGroup;
private error_messages:any;
private isAlreadyPresent:string=null;
  constructor(private fb:FormBuilder,private errorMessage:getErrorMessage,private authenticate:AuthenticationService) { }

  ngOnInit() {
    this.error_messages=this.errorMessage.geterror();
    this.userForm=this.fb.group({
      email:['',[Validators.required,Validators.email]],
      password:['',[Validators.required,Validators.minLength(5),Validators.maxLength(40),
        Validators.pattern(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{5,}$/)]]
    })
  }


  onSubmit(form:NgForm){
    this.authenticate.login(form).subscribe(data=>{
     
      if(data.custToken!=null){
        this.isAlreadyPresent=null
        localStorage.setItem('custToken',data.custToken);
        window.location.reload();
      }
    },(err)=>{
    //console.log(err);
    this.isAlreadyPresent=err.message;
    })
    //console.log(form);
  }

}
