import { Component, OnInit,Input,Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'cust-subproduct-amt-price',
  templateUrl: './subproduct-amt-price.component.html',
  styleUrls: ['./subproduct-amt-price.component.css']
})
export class SubproductAmtPriceComponent implements OnInit {
@Input() set Amtpiceobj(val){
this.amtpriceobj=val;
}

@Output() selectAmt= new EventEmitter();
private discount=null;
private sellprice=null;
private amtpriceobj=null;
  constructor() { }

  calculate(){
    this.discount= parseFloat(this.amtpriceobj.discount).toString();
    this.sellprice=((parseFloat(this.amtpriceobj.price)-(0.01*parseInt(this.amtpriceobj.discount)*parseFloat(this.amtpriceobj.price))).toFixed(2));
  }

  ngOnInit() {
this.calculate();
  }
  setThisAmt(){
this.selectAmt.emit(this.amtpriceobj);
  }
}
