import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class CustomerDataService{
  private orderurl=url.getCustOrder
private customerdataurl= url.customerData;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };


   getProfileData():Observable<any>{
        return this.http.get(this.customerdataurl).pipe(catchError(this.handleError));
    }
    getOngoingOrder():Observable<any>{
      return this.http.get(this.orderurl).pipe(catchError(this.handleError));
  }
  getCompletedOrdr():Observable<any>{
    return this.http.get(this.orderurl).pipe(catchError(this.handleError));
  }
}
