import { Component, OnInit } from '@angular/core';
//import { CategoryJsonretriever } from "../../../globalServices/categorylistjson";
//import {category} from "../../../globalmodel/listitem/category";
import {of as ObservableOf, BehaviorSubject} from "rxjs";
import {CategoryJsonretriever} from '../../../../globalServices/categorylistjson'
import { categoryList } from '../../moduleServices/categoryList';
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css'],
  providers:[CategoryJsonretriever]
})
export class NavbarComponent implements OnInit {
  private categorylist=[];
  private subcategory=[];
  private products=[];
  constructor(private categories:CategoryJsonretriever,private categoryListSubject:categoryList){}
 
    
      /*constructor(private categoryjson:CategoryJsonretriever){
          this.categoryjson.jsonretrieve().subscribe(data=>{
             this.categorylist=data;
              
          })
  }*/
  ngOnInit() {
      this.categories.jsonretrieve().subscribe(data=>{
          if(data!=null){
              this.categorylist=data;
              this.categoryListSubject.setcategoryList(this.categorylist);
             this.subcategory =this.categorylist[0].subcategory;
             this.products=this.subcategory[0].products;


          }
      })
  }
  
 
  changeCategory(category){
    this.subcategory=category.subcategory;
    this.changeSubcategory(this.subcategory[0]);
  }
  changeSubcategory(subcategory){
    this.products=subcategory.products;
  }

}
