import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, Form, NgForm } from '@angular/forms';
import { AuthenticationService } from '../login-signup/services/authenticateUser';
import { getErrorMessage } from 'src/app/globalServices/Validation';


@Component({
  selector: 'customer-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css'],
 
})
export class SignUpComponent implements OnInit {
private userForm:FormGroup;
private error_messages:any;
private isAlreadyPresent:String=null;
  constructor(private fb:FormBuilder,private authenticate:AuthenticationService,
    private errorMessage:getErrorMessage) { }

  ngOnInit() {
    this.error_messages=this.errorMessage.geterror();
    this.userForm=this.fb.group({
      firstName:['',[Validators.required]],
      lastName:['',[Validators.required]],
      email:['',[Validators.required,Validators.email]],
      password:['',[Validators.required,Validators.minLength(5),Validators.maxLength(40),
        Validators.pattern(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{5,}$/)]]
    })
  }


signUp(customerData:NgForm){
  this.authenticate.authenticateUser({customerData}).subscribe(data=>{
//console.log(data);
this.isAlreadyPresent=null;
    if(data.custToken!=null){
      localStorage.setItem('custToken',data.custToken);
     window.location.reload();

    }
  },err=>{
   // alert(err);
    this.isAlreadyPresent=err.message;
   
  })
}



}
