import { Component, OnInit,Input } from '@angular/core';
import { AddDelService } from '../../../sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';
import { cartProduct } from 'src/app/globalmodel/cartProduct';
import { productCardDTO } from '../../moduleServices/productCartDto';

@Component({
  selector: 'basket-item',
  templateUrl: './basket-item.component.html',
  styleUrls: ['./basket-item.component.css']
})
export class BasketItemComponent implements OnInit {
@Input() cartItem:cartProduct;
private savings:number=0;
  constructor(private addelservice:AddDelService,private productDto:productCardDTO) {

   }

  ngOnInit() {
    this.savings=parseFloat(((this.cartItem.CostPr-this.cartItem.sellPr)*this.cartItem.Qua).toFixed(2));

  }
  removeFromCart(){
    var obj={
        cartProductId:this.cartItem.CartPId
    }
    this.addelservice.deleteCartProduct(obj).subscribe(data=>{
        if(data.isDeleted==true){
            this.productDto.setCartStatus(true);
        }
    })
}

  setquantity(index){
    this.cartItem.Qua=this.cartItem.Qua+index;
    this.cartItem.SubT=this.cartItem.Qua*this.cartItem.sellPr;
    if(index==1){
    this.addelservice.cartCrud({ 'crud':'add' ,'cartProduct':this.cartItem}).subscribe(data=>{
     if(data.isPushed==true){
    this.productDto.setCartStatus(true);
    }})}
    if(index==-1){
     this.addelservice.cartCrud({ 'crud':'del' ,'cartProduct':this.cartItem}).subscribe(data=>{
    if(data.isDeleted==true){
        this.productDto.setCartStatus(true);

    }})}
}

  
}
