import { Component,OnInit,Input } from "@angular/core";
import { FormGroup,FormControl, Validators,FormGroupDirective,NgForm } from '@angular/forms';
import { getErrorMessage } from "../../../globalServices/Validation";
import { DoEmployeeelogin } from "./employeeloginservice/dologin";
import { Router } from '@angular/router';
@Component({
    selector:'logindetails',
    templateUrl:'login.component.html',
    styleUrls:['login.component.css'],
    
})


export class loginComponent implements OnInit{
   error_messages:any;
   tokenid:string='';
    loginDetails:FormGroup;
    constructor(private geterrormessage:getErrorMessage,private loginform:DoEmployeeelogin,private route:Router){
this.error_messages= this.geterrormessage.geterror();
    }
  ngOnInit(){ 
    if(localStorage.getItem('employee_id_token')!='-1' && localStorage.getItem('employee_id_token')!= null){
        this.tokenid=localStorage.getItem('employee_id_token');
    this.route.navigate(['/employee/dashboard'])
    }
    else{
 
    this.loginDetails= new FormGroup({
        email: new FormControl(null,[Validators.required,Validators.email]),
  password : new FormControl(null,[Validators.required,Validators.minLength(5),Validators.pattern("^(?=.*\d).{5,40}$")])
      
})
}}
onLogin(form:NgForm){
this.loginform.login(form).subscribe(data=>{
    alert('success');
}
,err=>{
    alert(err);
})

}

} 