import { Component, OnInit } from '@angular/core';
import { CustomerDataService } from '../../moduleServices/customerData';
import { ActivatedRoute } from '@angular/router';
import { OrderService } from '../../moduleServices/orderService';
import {day} from '../../../../globalmodel/DayMonth'
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'app-order-placed',
  templateUrl: './order-placed.component.html',
  styleUrls: ['./order-placed.component.css'],
  providers:[OrderService]
})
export class OrderPlacedComponent implements OnInit {
private order:any=null;

private router:any;
  constructor(private route:ActivatedRoute,private orderCrud:OrderService,private snackBar:MatSnackBar) { }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }


  ngOnInit() {
  this.router=this.route.params.subscribe(params=>{
    // console.log(params['id']);
     this.orderCrud.getSingleOrder({orderId:params['id']}).subscribe(data=>{
       if(data.status=='S'){
         this.order=data.order;
       //  console.log(this.order);
      
        // console.log(this.delivday);
       }
     },err=>{
       if(err.message!=null){
        this.openSnackBar(err.message,'');
       }else{
 this.openSnackBar('Unknown Error','');
       }
 
     })

    })
  
  }

  ngOnDestroy() {
    this.router.unsubscribe();
  }

}