import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OrderService } from '../moduleServices/orderService';
import { DelLevelService } from './services/delLevelService';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-delivery-status',
  templateUrl: './delivery-status.component.html',
  styleUrls: ['./delivery-status.component.css'],
  providers:[OrderService,DelLevelService]
})
export class DeliveryStatusComponent implements OnInit {
private order:any;
  private router:any;
  private stobj:any=null;
  private expobj:any=null;
  private standardDellevels:any=null;
  private expressDellevels:any=null;
  constructor(private route:ActivatedRoute,private orderCrud:OrderService,
    private delService:DelLevelService,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }


  getDelLevels(){
    if(this.order.deliverystatus.standardStatus!=null){
      this.delService.getLevelData({id:this.order.deliverystatus.standardStatus,delType:'standardStatus'}).subscribe(data=>{
        if(data.status=='S'){
       //  console.log(data);
this.standardDellevels=data.level.deliveryDates;
this.stobj=data.deldoc;
        }else{
          this.openSnackBar('Error Occur','');
        }
      },err=>{
        if(err.message!=null){
          this.openSnackBar(err.message,'');
          }else{
            this.openSnackBar(err,'');
          }
      })
    }
    if(this.order.deliverystatus.expressStatus!=null){
      this.delService.getLevelData({id:this.order.deliverystatus.expressStatus,delType:'expressStatus'}).subscribe(data=>{
        if(data.status=='S'){
this.expressDellevels=data.level.deliveryDates;
this.expobj=data.deldoc;
}else{
  this.openSnackBar('Error Occur','');
}
},err=>{
  if(err.message!=null){
    this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar(err,'');
    }
})
}

  }
  ngOnInit() {


  this.router=this.route.params.subscribe(params=>{
     // console.log(params['id']);
     this.orderCrud.getSingleOrder({orderId:params['id']}).subscribe(data=>{
      if(data.status=='S'){
        this.order=data.order;
       this.getDelLevels();

      }else{
        this.openSnackBar('Error Occur','');
      }
  },err=>{
    if(err.message!=null){
      this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar(err,'');
    }
  })
})
}
}

