import { Component, OnInit, OnDestroy } from '@angular/core';
import {productCardDTO} from '../../moduleServices/productCartDto';
import { cartDataService } from '../../moduleServices/cartDataService';
import { cartProduct } from 'src/app/globalmodel/cartProduct';
import { comments } from 'src/app/globalmodel/comments';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { IneligibleCartDialogComponent } from '../ineligible-cart-dialog/ineligible-cart-dialog.component';
import { AddDelService } from 'src/app/modules/sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';
@Component({
  selector: 'cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css'],
  providers:[AddDelService]

  
})
export class CartComponent implements OnInit{
private customerid:string;
private totalBill:number=0;
private cart:cartProduct[]=[];
private emptyMessage=comments.emptycart;
private isEmpty:boolean=true;
  constructor(private router:Router,private productdto:productCardDTO,
    private cartDataService:cartDataService,private cartdto:productCardDTO,
    private dialog:MatDialog,private cartCrud:AddDelService) { 
    
  }


  goToBasket(){
this.router.navigate(['/customer/basket']);
  }
  checkIfEmpty(){
   return this.isEmpty;
  }

  setcartProduct(dataelement,index){
   this.cart[index]= new cartProduct();
  let cartItem =this.cart[index];
  cartItem.Amt=dataelement.amount;
  cartItem.CartPId=dataelement.cartProductId;
 cartItem.CostPr=dataelement.costprice;
 cartItem.ImageUrl=dataelement.imageUrl;
 cartItem.Qua=dataelement.quantity;
 cartItem.sellPr=dataelement.sellprice;
 cartItem.SubT=dataelement.subTotal;
 cartItem.CId=dataelement.categoryId;
 cartItem.CName= dataelement.categoryName;
 cartItem.SubPId=dataelement.subproductId;
 cartItem.SubPName=dataelement.subproductName;
 cartItem.Suffix=dataelement.suffix;
 cartItem.Brand=dataelement.brand;
 cartItem.IsExp=dataelement.isExpress;

  }

  callbackend(){
    this.cartDataService.getCartData().subscribe(data=>{
      if(data.cartArray.length!=0){
       // console.log(data.cartArray);
        this.cartdto.setCartArray(data.cartArray);
         this.totalBill=0;
          this.cart=[];
        for(let i=0;i<data.cartArray.length;i++){                  //set the cartArray and get the changes to go to product card                                                            //productcard will take the changes and select the appropiate product whose changes r taking place
          let cartdataelement=data.cartArray[i]; 
         // console.log(cartdataelement);
         this.totalBill=parseFloat((this.totalBill+cartdataelement.subTotal));
          this.setcartProduct(cartdataelement,i);                   //setcartItems
        //  console.log(this.cart);
          this.isEmpty=false;
     
        }
     if(data.ineligibleCartArray.length!=0){
    let dialogRef=this.dialog.open(IneligibleCartDialogComponent,{
      position:{top:"0px"},
      width:"500px",
      height:"auto",
      data:data.ineligibleCartArray
    })
   dialogRef.afterClosed().subscribe(result=>{
     if(result==true){
       let idArray=(<Array<any>>data.ineligibleCartArray).map(element=>{
       return element.cartProductId;
       })
       console.log(idArray);
      this.cartCrud.emptyBasket({'cartIdArray':idArray}).subscribe(data=>{
        if(data.status=='EMP'){
          if(data.isEmpty==true){
            this.callbackend();
          }
        }
      })

     }
   })

      }

       }else{
         this.cartdto.setCartArray(['Empty']);                //if empty then product card will not be selected;
     
         this.isEmpty=true;
     
       }
       
     },err=>{
       this.cartdto.setCartArray(['Empty']);
     })
  }

  ngOnInit() {
    if(localStorage.getItem('custToken')!=null){
      this.callbackend();
    this.productdto.returncart().subscribe(data=>{
      if(data!=false){
this.callbackend();
      }
    })
  }else{
    this.cartdto.setCartArray(['Empty']);
  }}


}
