import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class OrderSumService{
    private url=url.getOrderSum;
private getvoucherUrl=url.getCustVouchers;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };


    getOrderSum(paramobj):Observable<any>{
        return this.http.post(this.url,paramobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    getAllVouchers():Observable<any>{
      return this.http.get(this.getvoucherUrl).pipe(catchError(this.handleError));
    }


}