import { Component, OnInit } from '@angular/core';
import { categoryList } from '../moduleServices/categoryList';
import { ActivatedRoute } from '@angular/router';
import { SubProductService } from './services/getSubproduct';

@Component({
  selector: 'app-pdpage',
  templateUrl: './pdpage.component.html',
  styleUrls: ['./pdpage.component.css'],
  providers:[SubProductService]
})
export class PdpageComponent implements OnInit {
  private errMess:String=null;
  private category:any=null;
  private subcategory:any=null;
  private productArray:Array<any>=null;
  private 
private wholecategoryList:Array<any>=null;
private subproductData:any=null;
  constructor(private categoryListSubject:categoryList,
    private route:ActivatedRoute,private subproductService:SubProductService) { }


setOutputCategoryAndItsFields(){
  this.category=null;
  this.subcategory=null;
  this.productArray=null;
for(let category of this.wholecategoryList){
  //console.log(category,this.subproductData);
   if(this.subproductData.categoryId==category.categoryId){
     this.category= {
       name:category.categoryName,
       id:category.categoryId
     }
    for(let subcat of category.subcategory ){
      if(subcat.subcategoryId==this.subproductData.subcategoryId){
        this.subcategory={
          name:subcat.subcategoryName,
          id:subcat.subcategoryId
        }
        this.productArray=subcat.products;
        return;
      }
    }
  }
   }

}

  ngOnInit() {
  this.route.params.subscribe(params=>{
    if(params!=null){
      //console.log(params);
      let id=params['id'];
      //console.log(id);
          this.subproductService.getSP({'id':id}).subscribe(data=>{
            if(data.status=='S'){
              //console.log(data);
              this.subproductData=data.subproduct;
              if(this.wholecategoryList!=null){               
                this.setOutputCategoryAndItsFields();
              }

            }else{
              alert('Unknown Error')
            }
          },err=>{
            if(err.message!=null){
                alert(err.message);
            }else{
              alert(err);
            }
          })
    }
  })




    this.categoryListSubject.getBehaviourSubject().subscribe(data=>{
      //console.log(data);
      if(data!=null){
        
   if(data.length!=0){
    this.wholecategoryList=data;
    if(this.subproductData!=null){
     // //console.log('yo');
      this.setOutputCategoryAndItsFields();
    }
   }else{
    this.errMess="No Data Found";
          }

      }
    })
  }

  


}
