import { Component, OnInit,Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
@Component({
  selector: 'app-ineligible-cart-dialog',
  templateUrl: './ineligible-cart-dialog.component.html',
  styleUrls: ['./ineligible-cart-dialog.component.css']
})
export class IneligibleCartDialogComponent implements OnInit {
private falseCart:Array<any>=null;
  constructor(private dialogRef:MatDialogRef<IneligibleCartDialogComponent>,@Inject(MAT_DIALOG_DATA) private data: any) { }

  ngOnInit() {
  this.falseCart=this.data;
  }

  removeThem(){
    this.dialogRef.close(true);
  }
}
