import { Component, OnInit, EventEmitter ,Output, OnDestroy} from '@angular/core';
import { cartDataService } from '../../moduleServices/cartDataService';
import { cartProduct } from 'src/app/globalmodel/cartProduct';
import { productCardDTO } from '../../moduleServices/productCartDto';
import { AddDelService } from '../../../sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'basket-table',
  templateUrl: './basket.component.html',
  styleUrls: ['./basket.component.css'],
  providers:[AddDelService]
})
export class BasketComponent implements OnInit,OnDestroy {
private cart:cartProduct[]=[];
private cartIdArray:any[]=[];
private isEnd:boolean=false;
private categoryWiseCart:any[]=[];
private cartLength:number=0;
private updatedIndex:number=0;
private categoryrecord:any[]=[];
private isFoundNewIndex:boolean=false;
private totaldiscount:number=0;
@Output() totalpriceemit:EventEmitter<any>=new EventEmitter();
private totalprice:number=0;
//private cartArray:cart
  constructor(private cartData:cartDataService,private productdto:productCardDTO,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

ngOnInit(){
  
  this.productdto.returncart().subscribe(data=>{
    if(data!=false){
      
      this.backendCartCall();
    }else{
      this.backendCartCall();
    }
  })
}

initialize(){
  this.totalprice=0;
  this.totaldiscount=0;
  this.cart=[];
  this.categoryWiseCart=[];
  this.updatedIndex=0;
  this.categoryrecord=[];
  this.cartIdArray=[];
  this.cartLength=0;
}

  backendCartCall()
 { this.isEnd=false;
this.cartData.getCartData().subscribe(data=>{
  this.initialize();
  
  if(data!=null){
    if(data.cartArray.length!=0){
    this.setcartdata(data.cartArray);
   //this.categoryrecord.push(this.cart[0].CName)
    for(let j=0;j<=(data.cartArray.length);j++){
     if(this.isEnd!=true){
      this.setcategorywise();
  }else{
    return;
  }}
//console.log(this.categoryWiseCart);
}
}


 // console.log(data);
},err=>{
  this.openSnackBar(err.message,'');
  
})

  }
  setcategorywise(){
    this.categoryWiseCart.push(this.splitarraycategorywise(this.cart));
  
  }



  splitarraycategorywise(cartArray){     //update index from where the cartarray has found new category and will return obj with it
    
   this.isFoundNewIndex=false;

    let cartlength=0;
    let partcartprice=0;
    let currentname=cartArray[this.updatedIndex].categoryName;
    let tempcartlist=[];
    let index=0;
for(let i=this.updatedIndex; i<cartArray.length;i++){
  let cartitem=cartArray[i];
 
  if(cartitem.categoryName==currentname){
   tempcartlist.push(cartitem);
   cartlength++;
   partcartprice+=cartitem.sellPr;

  }else{
    //console.log(this.categoryrecord,cartitem.categoryName,(this.categoryrecord.findIndex(categoryname=> categoryname==cartitem.categoryName)))
    if(this.isFoundNewIndex!=true && 
      (this.categoryrecord.findIndex(categoryname=> categoryname==cartitem.categoryName))==-1){
    index=i;
    this.isFoundNewIndex=true;     //to check if any new category found if found set to true
                                   //and next time the function loop will start from there the updatd index
   
}
  }
}

// console.log(this.isFoundNewIndex);   //if no more category left stop the loop by set the end to true
this.updatedIndex=index;
if(this.isFoundNewIndex!=true){
  this.isEnd=true;
  
}
  this.categoryrecord.push(currentname);  //still have to push the list formed 
  //console.log(tempcartlist)
return {categoryName:currentname, 'cartlength':cartlength,'partcartPrice':partcartprice ,cartlist:tempcartlist};
 
}

  setcartdata(cartArray){
    
    this.cartLength=cartArray.length;
    for(let cartitem of cartArray){
  let cartobj= new cartProduct();
  cartobj.Amt=cartitem.amount;
  cartobj.CartPId=cartitem.cartProductId;
 cartobj.CostPr=cartitem.costprice;
 cartobj.ImageUrl=cartitem.imageUrl;
 cartobj.Qua=cartitem.quantity;
 cartobj.sellPr=cartitem.sellprice;
 cartobj.SubT=cartitem.subTotal;
 cartobj.CId=cartitem.categoryId;
 cartobj.CName= cartitem.categoryName;
 cartobj.SubPId=cartitem.subproductId;
 cartobj.SubPName=cartitem.subproductName;
 cartobj.Suffix=cartitem.suffix;
 cartobj.Brand=cartitem.brand;
 this.cartIdArray.push(cartitem.cartProductId);
 this.totalprice+=cartitem.subTotal;

 this.totaldiscount+=parseFloat(((cartitem.costprice-cartitem.sellprice)*cartitem.quantity).toFixed(2))

this.cart.push(cartobj);
    }

    this.totalpriceemit.emit({'price':this.totalprice,'discount':this.totaldiscount, 'cartIdArray':this.cartIdArray});
  }

  ngOnDestroy(){
   // this.productdto.returncart().unsubscribe();
  }

}
