import {Injectable} from '@angular/core';
import {url} from '../../../../../globalmodel/url';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import { Observable,throwError } from 'rxjs';
import { httpOptions } from "../../../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';



@Injectable()

export class AddDelService{
  private emptycart=url.emptycart;
    private deleteurl=url.deletecartProduct;
private addurl = url.addtoCart;
private deducturl= url.deleteFromCart;
    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } 
            else if(error.status!=0){
              err=error.error;
            }else{
              err=comments.networkError;
            }
      
              return throwError(
          err);
             };

             deleteCartProduct(idobj):Observable<any>{
              return this.http.post<any>(this.deleteurl,idobj,httpOptions.jsonHeaders).pipe(
                catchError(this.handleError));   
             }

             emptyBasket(idobj){
                return this.http.post<any>(this.emptycart,idobj,httpOptions.jsonHeaders).pipe(
                  catchError(this.handleError));    
              }
             


    cartCrud(details:any):Observable<any>{
      if(details.crud=='add'){
   return this.http.post<any>(this.addurl,details,httpOptions.jsonHeaders).pipe(
   catchError(this.handleError));    
      }else if(details.crud=='del'){
        return this.http.post<any>(this.deducturl,details,httpOptions.jsonHeaders).pipe(
          catchError(this.handleError));    
      }

 }


}