import {Component, OnInit,Inject} from '@angular/core';
import {MAT_DIALOG_DATA,MatDialogRef} from '@angular/material';
@Component({
  selector: 'app-vouchers-dialog',
  templateUrl: './vouchers-dialog.component.html',
  styleUrls: ['./vouchers-dialog.component.css']
})
export class VouchersDialogComponent implements OnInit {
private voucherArray:Array<any>=null;
private sendVoucherCode=null;
private currentSelectedIndex:number=null;
  constructor(@Inject(MAT_DIALOG_DATA) private data: any,private dialogRef:MatDialogRef<VouchersDialogComponent>) { }

  ngOnInit() {
this.voucherArray=this.data;

  }

selectThis(obj){
  console.log(obj);
this.sendVoucherCode=obj.code;
this.currentSelectedIndex=obj.pos;
}

applyVoucher(){
  this.dialogRef.close({voucherCode:this.sendVoucherCode});
}



}
