import { Component, OnInit,Input,Output, EventEmitter} from '@angular/core';
import { MatDialog } from '@angular/material';
import { TimeSlotComponent } from '../time-slot/time-slot.component';
import {day,month} from '../../../../globalmodel/DayMonth'
@Component({
  selector: 'cust-expressdeliv',
  templateUrl: './expressdeliv.component.html',
  styleUrls: ['./expressdeliv.component.css']
})
export class ExpressdelivComponent implements OnInit {

  @Input() set CartArray(val){
    this.cartArray=val;
    this.calcLength();
    };
    @Output() setDelTimeSlot=new EventEmitter();
    private amountPay=0;
   
    @Input() set AmtPay(val)
    {this.amountPay=val;
    }
    private expbasketlength:Number=0;
  private standprice:any=0;
  private expressprice:any=0;
  private cartArray:any[]=[];
  private date=new Date(Date.now());
  private dateobj;
  private starr=[];
  private exparr=[];
  private standardbasketlength:Number=0;
  private timeSlot;
  private productdelivobj;
  private sttimeSlot=null;
  private exptimeSlot=null;
  
  
  constructor(private dialog:MatDialog) { }

  calcLength(){
    this.standprice=0;
    this.expressprice=0;
    this.starr=[];
  this.exparr=[];
    this.starr=this.cartArray.filter(cartitem=>{
      if(cartitem.isExpress==false){
       this.standprice+=cartitem.subTotal;
      
        return cartitem;
      }else{
        this.exparr.push(cartitem);
       
        this.expressprice+=cartitem.subTotal;
      
      }
    });
    this.standardbasketlength=this.starr.length;
    this.expbasketlength=this.exparr.length;
    this.setDate();
    if(this.starr.length==0){
      this.productdelivobj={
        type:'EXP',
        'EXP':this.timeSlot
      };
    }else{
      this.productdelivobj={
        type:'EXP',
        'ST':this.timeSlot,
        'EXP':this.timeSlot
      };
    }
    
    this.setDelTimeSlot.emit(this.productdelivobj);
   
  }
setDate(){
  this.date.setDate(this.date.getDate()+1);
    this.dateobj={
      'Fulldate':this.date,
      'Day':'Tomorrow',
      'Date':this.date.getDate(),
      'Month':month[this.date.getMonth()]
    }
    this.timeSlot={
      date:this.dateobj,
      startTimeSlot:'7.00AM',
      endTimeSlot:'9.00AM'
    }

    this.sttimeSlot=this.timeSlot;
    this.exptimeSlot=this.timeSlot;
  
}

  ngOnInit() {
  
 
  }

  opendialog(type){
    const dialogRef=this.dialog.open(TimeSlotComponent,{
      data:type,
      width:'55%',
      height:'25%',
     
    
    })
    dialogRef.afterClosed().subscribe(data=>{
      if(data!=null){
    this.productdelivobj[data.type]=data.timeslot;
    if(data.type=='ST'){
      this.sttimeSlot=data.timeslot;
      
    }else{
      this.exptimeSlot=data.timeslot;
    }
    this.setDelTimeSlot.emit(this.productdelivobj);

    }})
  }
   
 
}

