import {Injectable} from '@angular/core';
import {url} from '../../../../globalmodel/url';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import { Observable,throwError } from 'rxjs';
import { httpOptions } from "../../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';



@Injectable()

export class SearchService{
    

    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } 
            else if(error.status!=0){
              err=error.error;
            }else{
              err=comments.networkError;
            }
      
              return throwError(
          err);
             };
    searchquery(url:string,details:any,):Observable<any>{
        
   return this.http.post<any>(url,details,httpOptions.jsonHeaders).pipe(
   catchError(this.handleError));    
 }
}