import { Component, OnInit, EventEmitter,Output } from '@angular/core';
import { OrderSumService } from './services/orderSumAndVoucher';
import { MatSnackBar, MatDialog } from '@angular/material';
import { VouchersDialogComponent } from '../../vouchers-dialog/vouchers-dialog.component';
import { IneligibleCartDialogComponent } from '../../cartSystem/ineligible-cart-dialog/ineligible-cart-dialog.component';
import { AddDelService } from 'src/app/modules/sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';

@Component({
  selector: 'cust-order-sum',
  templateUrl: './order-sum.component.html',
  styleUrls: ['./order-sum.component.css'],
  providers:[OrderSumService,AddDelService]
})
export class OrderSumComponent implements OnInit {
  @Output() sendTotalAmt= new EventEmitter();
  @Output() sendCartArray =  new EventEmitter();
private basketValue:number=0;
private deliverycharges:number=50;
private voucherDiscount:number=null;
private discount:number=0;
private applicableVouchers:Array<any>=[];
private allvouchers:Array<any>=[];
private voucherCode:string=null;
private totalamountpayee:number=0;
  constructor(private orderSum:OrderSumService,
    private dialog:MatDialog,
    private snackBar:MatSnackBar,private cartCrud:AddDelService) { }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }


emittotalamount(){
  this.sendTotalAmt.emit(this.totalamountpayee);
}

emitCart(array){
this.sendCartArray.emit(array);
}


getApplicableVoucher(){
if(this.basketValue>100){
  this.applicableVouchers=this.allvouchers;
  //console.log(this.applicableVouchers);
}
}

getAllVouchers(){
  this.orderSum.getAllVouchers().subscribe(data=>{
    if(data.status=='S'){
      //console.log(data);
      if(data.vouchers.length!=0){
      this.allvouchers=data.vouchers[0].allVouchers;
      this.getApplicableVoucher();
    }
  }else{
    this.openSnackBar('Unknown Error','');

  }
  },err=>{
    if(err.message!=null){
      this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar(err,'');
    }
  })
}


getBackendOrderSum(){
  this.orderSum.getOrderSum({voucherCode:this.voucherCode}).subscribe(data=>{
   //console.log(data);
   if(data.status=='S'){
       if(data.voucherDiscount!=null){
         this.voucherDiscount=data.voucherDiscount;
       }
    this.basketValue= data.orderSum.basketValue;
    this.deliverycharges=data.orderSum.deliveryCharges;
    this.discount=data.orderSum.totalSavings;
    this.totalamountpayee=data.orderSum.totalAmountToPay;
   this.emittotalamount();
   this.emitCart(data.cartArray);
   if(this.allvouchers.length==0){
     this.getAllVouchers()
   }
   if(data.ineligibleCartArray.length!=0){
    let dialogRef=this.dialog.open(IneligibleCartDialogComponent,{
      position:{top:"0px"},
      width:"500px",
      height:"auto",
      data:data.ineligibleCartArray
    })
   dialogRef.afterClosed().subscribe(result=>{
     if(result==true){
       let idArray=(<Array<any>>data.ineligibleCartArray).map(element=>{
       return element.cartProductId;
       })
       //console.log(idArray);
      this.cartCrud.emptyBasket({'cartIdArray':idArray}).subscribe(data=>{
        if(data.status=='EMP'){
          if(data.isEmpty==true){
            this.getBackendOrderSum();
                    }
        }
      })

     }
   })

      }
   }
   
     
 },err=>{
  if(err.message!=null){
    this.openSnackBar(err.message,'');

  }else{
    this.openSnackBar(err,'');
  }
 })
}





  ngOnInit() {
 this.getBackendOrderSum();
}

openApplicableVouchers(){
  //let dialogRef= this.dialog.open();
  let dialogRef=this.dialog.open(VouchersDialogComponent,{
    width:"900px",
    height:"100%",
    data:this.applicableVouchers
  })
dialogRef.afterClosed().subscribe(res=>{
  if(res!=null){
    //console.log(res);
    this.voucherCode=res.voucherCode;
this.getBackendOrderSum();
  }
})
}


}
