import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { getErrorMessage } from 'src/app/globalServices/Validation';

@Component({
  selector: 'cust-gift-card',
  templateUrl: './gift-card.component.html',
  styleUrls: ['./gift-card.component.css']
})
export class GiftCardComponent implements OnInit {
  private firstName=null;
  @Input()  set firstValue(val){
    
this.firstName=val;
this.initialize();
}
@Output() setgiftForm= new EventEmitter();

  private giftForm:FormGroup;
  private error_messages=null;
  private inputoptions={
    hidecompulsion:false,
    floatlabel:'always'
  }
    
  constructor(private fb:FormBuilder,private errorMessage:getErrorMessage) { }
ngOnInit(){
  
}
  initialize() {
    console.log(this.firstName);
    this.error_messages=this.errorMessage.geterror();
    this.giftForm=this.fb.group({
      senderName:[this.firstName,[Validators.required]],
      recieverName:['',[Validators.required]],
      message:['',[Validators.required]]
    })
  }

  onSubmit(form:NgForm){
this.setgiftForm.emit(form);
  
  }
  cancel(){
this.setgiftForm.emit(null);
  }

}
