import { Component, OnInit } from '@angular/core';
import { productCardDTO } from '../../moduleServices/productCartDto';
import { AddDelService } from '../../../sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-basketpage',
  templateUrl: './basketpage.component.html',
  styleUrls: ['./basketpage.component.css'],
  providers:[AddDelService]
})
export class BasketpageComponent implements OnInit {
private totalprice:number=0;
private totaldiscount:number=0;
private obj={
  'cartIdArray':null
}
  constructor(private cartdto:productCardDTO,private emptycart:AddDelService,private router:Router,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

  ngOnInit() {
  }

  setTotalPrice(data:any){
this.totalprice=data.price;
this.totaldiscount=data.discount;
this.obj.cartIdArray=data.cartIdArray;
//console.log(this.obj.cartIdArray);
  }

  gotocheckout(){
   
  if(this.totalprice!=0 && this.obj.cartIdArray!=null){
this.router.navigate(["/customer/checkout"]);
}
}


  emptybasket(){

    this.emptycart.emptyBasket(this.obj).subscribe(data=>{
    
      if(data!=null){
        
        if(data.isEmpty==true){         //to again trigger backend call at basket page to get new Data 
 this.totaldiscount=0;
 this.totalprice=0;
 this.obj.cartIdArray=null;
          this.cartdto.setCartStatus(true);
          this.cartdto.setCartArray(["Empty"]);
        }
      }
    },err=>{
      this.openSnackBar(err.message,'');

    })

  }
}
