import {Pipe,PipeTransform} from '@angular/core';


@Pipe({name:'sortBy'})
export class sorByPipe implements PipeTransform{


transform(adArray:any[],type:string){
    
    if(!adArray || !type){
return adArray;
    }
else if(type=='asc'){
    adArray.sort((a,b)=>{
         if(a.priority<b.priority){
             return -1;
         }else if(a.priority>b.priority){
             return 1;
         }
         else {
        return 0;
         }
    })
    
    return adArray;


}else if(type='desc')
{
    adArray.sort((a,b)=>{
        if(a.priority>b.priority){
            return -1;
        }else if(a.priority<b.priority){
            return 1;
        }
        else {
       return 0;
        }
   })

   return adArray;
}
}
}