import { Component, OnInit,Input,Output, EventEmitter} from '@angular/core';
import { MatDialog } from '@angular/material';
import { TimeSlotComponent } from '../time-slot/time-slot.component';
import {day,month} from '../../../../globalmodel/DayMonth'
@Component({
  selector: 'cust-standdeliv',
  templateUrl: './standdeliv.component.html',
  styleUrls: ['./standdeliv.component.css']
})
export class StanddelivComponent implements OnInit {
  @Input() set CartArray(val){
    this.cartArray=val;
    this.calcLength();
    };
    @Output() setDelTimeSlot=new EventEmitter();
    private amountPay=0;
   
    @Input() set AmtPay(val)
    {this.amountPay=val;
    }
  
  private cartArray:any[]=[];
  private date=new Date(Date.now());
  private dateobj;
  private basketlength:Number=0;
  private timeSlot;
  private productdelivobj={
    type:null,
    'ST':null
  };
  
  
  constructor(private dialog:MatDialog) { }

  calcLength(){
    this.basketlength=this.cartArray.length;
  }


  ngOnInit() {
    this.date.setDate(this.date.getDate()+1);
    this.dateobj={
      'Fulldate':this.date,
      'Day':'Tomorrow',
      'Date':this.date.getDate(),
      'Month':month[this.date.getMonth()]
    }
    this.timeSlot={
      date:this.dateobj,
      startTimeSlot:'7.00AM',
      endTimeSlot:'9.00AM'
    }
    this.productdelivobj.type='ST';
   this.productdelivobj.ST=this.timeSlot;
   this.setDelTimeSlot.emit(this.productdelivobj);
 
  }

  opendialog(type){
    const dialogRef=this.dialog.open(TimeSlotComponent,{
      data:type,
      width:'55%',
      height:'25%',
     
    
    })
    dialogRef.afterClosed().subscribe(data=>{
      if(data!=null){
    this.timeSlot=data.timeslot;
    this.productdelivobj[data.type]=this.timeSlot;
    this.setDelTimeSlot.emit(this.productdelivobj);

    }})
  }
   
 
}

