import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class FillDataService{
    private fillcusturl=url.fillcustdetails;
private getDelivAddurl=url.getDelivAddress;
private setCuraddurl=url.setCurAdd;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

setCurrentAddress(addobj):Observable<any>{
  return this.http.post(this.setCuraddurl,addobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  
}


  fillcustdetails(filldetails):Observable<any>{
   return this.http.post(this.fillcusturl,filldetails,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
    getDelivAddress():Observable<any>{
        return this.http.get(this.getDelivAddurl).pipe(catchError(this.handleError));
    }
    


}