import { Component, OnInit, OnDestroy } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef, MatSnackBar} from '@angular/material';
import { MatDialog } from '@angular/material';
import { OtpService } from '../deliv-address/services/otpService';
@Component({
  selector: 'app-otp-dialog',
  templateUrl: './otp-dialog.component.html',
  styleUrls: ['./otp-dialog.component.css'],
  providers:[OtpService]
})
export class OtpDialogComponent implements OnInit,OnDestroy {
private otpNumber:number=null;
private errMess=null;
private isResend=false;
private timeRemain=null;
private setInt=null;
  constructor(private otpCrud:OtpService, 
   private dialogRef:MatDialogRef<OtpDialogComponent>,private snackBar:MatSnackBar) { }
   openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

generateOTP(){

  this.otpCrud.createOTP().subscribe(data=>{
   // console.log(data);
    if(data.status=='S' && data.isCreated==true){
      
      this.timeRemain=15;
//console.log(this.timeRemain);
     this.setInt= setInterval(()=>{
        if(this.timeRemain>0){
          this.timeRemain--;
        //  console.log(this.timeRemain)
        }
        if(this.timeRemain==0){
          if (this.setInt) {
            clearInterval(this.setInt);
          }
        }
      
      },1000)
   

    }else{
   this.openSnackBar('Unknown Error','');
    }
  },err=>{
    if(err.message!=null){
 this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar('Unknown Error','');
    }
  })
}

  ngOnInit() {
this.generateOTP();
    
    
  }
cancel(){
  this.dialogRef.close(null);
}

resend(){
  
 this.generateOTP();
 
}


  submit(){

    this.otpCrud.checkOtp({otp:this.otpNumber}).subscribe(data=>{
      if(data.status=='VALOTP' && data.isValid==true){
        this.dialogRef.close({
          valid:true
    
        })
      }else{
    this.errMess='Invalid OTP'
      }
    },err=>{
      this.errMess=err.message;
    })
   

   
  }
  ngOnDestroy(){
    if (this.setInt) {
      clearInterval(this.setInt);
    }
  }
     


}
