import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class OrderService{
   
  private getorderUrl=url.getSingleOrder;
private placeOrderUrl=url.placeOrder;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

placeOrder(orderdetails):Observable<any>{
  return this.http.post(this.placeOrderUrl,orderdetails,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  
}

getSingleOrder(id):Observable<any>{
  return this.http.post(this.getorderUrl,id,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  
}
}
