import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { productCardDTO } from 'src/app/modules/customerModule/moduleServices/productCartDto';
import { AddDelService } from './services/addelCrud';
import { cartProduct } from 'src/app/globalmodel/cartProduct';
import { MatDialog, MatDialogRef } from '@angular/material';
import {LoginSignupComponent} from '../../../customerModule/loginSystem/login-signup/login-signup.component';
import { Router } from '@angular/router';
@Component({
    selector:"adddel",
    styleUrls:['./adddel.component.css'],
    templateUrl:'./adddel.component.html',
    providers:[AddDelService]
})

export class AddDelComponent implements OnInit{
    @Input() product=null; 
    
    @Input() set productCart(val:any){
    this.productForCart=val;
    this.intializecheck();
    

    }
    @Input()stackTrace=null;
    @Output() amountChange:EventEmitter<number>= new EventEmitter();
    private productForCart:cartProduct=null;
    private changeCSS=false;
        private addedIndex:number=0;
        private currentIndex:any;
    private isadded:boolean=false;
    private dialogRef:MatDialogRef<any>;

    constructor(private productdto:productCardDTO,private router:Router, private addelservice:AddDelService,private dialog:MatDialog){
       
    }

setOrder(index){
    //  console.log(index);
    this.productForCart.Qua=this.productForCart.Qua+index;
    if(this.productForCart.Qua<0){
        this.productForCart.Qua=1;
    }
    this.productForCart.SubT =this.productForCart.Qua*this.productForCart.sellPr;

}



intializecheck(){
    if(this.productForCart.Qua==0){
        this.currentIndex=1;
        this.addedIndex=0;
        }else{
            this.currentIndex=this.productForCart.Qua;
            this.addedIndex=this.currentIndex;
            this.isadded=true;
          //  this.checkIfAddedIndexZero();
        //  this.amountChange.emit(this.addedIndex);

        }
}

opendialog(){
this.dialogRef=this.dialog.open(LoginSignupComponent,{
    width:'353px',
    height:'478px',
    data:0

})
}

checkChange(){
    return this.changeCSS;
}


    ngOnInit(){
        if(this.router.url.indexOf('/pd/') > -1){
            this.changeCSS=true;
        }
      //  console.log(this.productForCart);
      this.intializecheck();
    }
    checkIfAddedIndexZero(){
    
        if(this.addedIndex>0){
          this.isadded=true;
        }
        if(this.addedIndex==0){
          this.isadded=false;
        }
        return this.isadded;
      }
    
      addToCart(index){
     if(localStorage.getItem('custToken')!=null){
                this.setOrder(index);
       if(localStorage.getItem('custToken')!=null){
       // console.log(this.productForCart);
        this.addelservice.cartCrud({ 'crud':'add' ,'stackTrace':this.stackTrace ,'cartProduct':this.productForCart}).subscribe((data)=>{
            if(data.isPushed==true){
              //  console.log(data);
                this.addedIndex++;      //addedIndex is the frontend index can be 0 or more;
                this.currentIndex=data.cartproduct.quantity;
              //  console.log(this.currentIndex);
                this.productForCart.CartPId=data.cartproduct.cartProductId;
                this.productForCart.Qua=data.cartproduct.quantity;
             //   console.log(this.productForCart);
                this.productdto.setCartStatus(true);  //set change has happend and again call from backend
                this.amountChange.emit(this.addedIndex);  //emit the change has happed for colorchange of product card
            }
        },err=>{
             if(err.message!=null){
            this.productForCart.Qua=this.productForCart.Qua-index;
            this.productForCart.SubT =this.productForCart.Qua*this.productForCart.sellPr;
            this.opendialog();
        }
            else{
                alert(err);
            }
        })
       }
     // this.checkIfAddedIndexZero();}
    }else{
this.opendialog();
    }
    }
    //initially the current index and addedIndex differ by one bt after adding the equal 
    //on deletion they again differ by one when that product is not in the cart entirely(cartProduct)
    deleteFromCart(index){
        if(localStorage.getItem('custToken')!=null){
        this.setOrder(index);
        if(localStorage.getItem('custToken')!=null){
        this.addelservice.cartCrud({ 'crud':'del' ,'stackTrace':this.stackTrace ,'cartProduct':this.productForCart}).subscribe((data)=>{
            if(data.isDeleted==true){
                console.log(data);
                this.addedIndex--;  
                this.currentIndex=data.cartproduct.quantity;      //currentindex is the index returned by the backend and is always equal to 1                                                      //or more and last deleted product is returned of the delted product
                this.amountChange.emit(this.addedIndex);  
                this.productdto.setCartStatus(true)};
                if(this.addedIndex!=0){
                //  console.log(this.currentIndex);
                 this.productForCart.CartPId=data.cartproduct.cartProductId;
                  this.productForCart.Qua=data.cartproduct.quantity;
                 
                }else{
                    this.productForCart.CartPId=null;   //completed deleted product
                    this.productForCart.Qua=0;
                }
                  
            },err=>{
                this.productForCart.Qua=this.productForCart.Qua+index;
                this.productForCart.SubT =this.productForCart.Qua*this.productForCart.sellPr;
            
                alert(err);
            })
      //this.checkIfAddedIndexZero();
    
        }}else{
            this.opendialog();
        }
    }
}