import { Component, OnInit,Input,Output, EventEmitter,AfterViewInit, ViewChild } from '@angular/core';
import { MatTabChangeEvent } from '@angular/material';
@Component({
  selector: 'cust-deliv-options',
  templateUrl: './deliv-options.component.html',
  styleUrls: ['./deliv-options.component.css']
})
export class DelivOptionsComponent implements AfterViewInit,OnInit {
@Input() set CartArray(val){
this.cartArray=val;
this.setcondition();
};
@Output() setTimeSlot=new EventEmitter();
private amountPay=0;
@Input() set amtPay(val)
{this.amountPay=val;
}
private index=0;
private selectedIndex=0;
private timeslotArr=[];
private cartArray:any[]=[];
private standardbasketlength:Number=0;
private expbasketlength:Number=0;

setcondition(){
  let starr=[];
let exparr=[];
starr=this.cartArray.filter(cartitem=>{
  if(cartitem.isExpress==false){   
    return cartitem;
  }else{
    exparr.push(cartitem);
   
  }
});
this.standardbasketlength=starr.length;
this.expbasketlength=exparr.length;
}
  ngOnInit() {
this.setTimeSlot.emit(null);

  }
  tabChanged = (tabChangeEvent: MatTabChangeEvent): void => {
    this.setTimeSlot.emit(this.timeslotArr[tabChangeEvent.index]);
    this.selectedIndex=tabChangeEvent.index;
  }
  ngAfterViewInit() {
  
   
  }

  setTimeSlotAndType(data){
   // console.log(data);
   
if(data.type=='ST'){
  this.timeslotArr[this.index]=data;
 this.index++;
  
}
else if(data.type=='EXP'){
  if(this.index>0){
    this.timeslotArr[1]=data;
  this.index=0;
  }else{
  this.timeslotArr[this.selectedIndex]=data;

  }
}
console.log(this.selectedIndex);
this.setTimeSlot.emit(this.timeslotArr[this.selectedIndex]);

  }





}
