import { Component, OnInit } from '@angular/core';
import { dataStorage } from '../moduleServices/datastorage';
import { SubProduct } from 'src/app/globalmodel/listitem/subProduct';
import { PushList } from './services/pushList';
import {snackbar} from '../moduleServices/snackbar';
@Component({
  selector: 'app-selected-category',
  templateUrl: './selected-category.component.html',
  styleUrls: ['./selected-category.component.css'],
  providers: [PushList]
})
export class SelectedCategoryComponent implements OnInit {
  private list:Array<SubProduct>=[];
  
  private finalList:Array<SubProduct>=[];
  constructor(private datalist:dataStorage,private pushList:PushList,private snackBar:snackbar) { }

  ngOnInit() {
    this.list = this.datalist.getdata();

  }

  change(e,data,index){
    if(e.checked){
     this.finalList.push(data);
    }
    else if(!e.checked){
      this.finalList.splice(index,1);
    }
  }

  submitList(){
    if(this.finalList.length!=0){
    this.pushList.pushlist(this.finalList).subscribe(data=>{
       this.snackBar.openSnackBar('Submitted','');
    },err=>{
  this.snackBar.openSnackBar(err,'');
    })
  }
}
}
