import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class AuthenticationService{
    private signUpurl=url.customerSignUp;
private signUpViaTab= url.customerloginSignUpTabs;
private custLogin = url.customerLogin;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

login(user):Observable<any>{
  return this.http.post(this.custLogin,user,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
}

    authenticateUser(user):Observable<any>{
        return this.http.post(this.signUpurl,user,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    authenticateviaSocialTabs(typeofSignUp):Observable<any>{
      let url = this.signUpViaTab+typeofSignUp;
   return this.http.get(url).pipe(catchError(this.handleError));
    }


}