import { Component, OnInit } from '@angular/core';
import {maxproductexcelfiles} from '../../../../globalmodel/maxExcelFile';
import { excelUpload } from '../../../../globalServices/excelUpload';
import {SuccessComponent} from '../../successSnackbar';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-excelupload',
  templateUrl: './excelupload.component.html',
  styleUrls: ['./excelupload.component.css'],
  providers:[excelUpload]
})
export class ExceluploadComponent implements OnInit {
private formdata:FormData;
private formcount=0;
private isSelected:boolean=false;
  constructor(private excelUpload:excelUpload,private snackBar: MatSnackBar) { }

  ngOnInit() {
    this.formdata = new FormData();
    this.formdata.set('idToken',localStorage.getItem('id_token'))
  }
  openSnackBar() {
    this.snackBar.openFromComponent(SuccessComponent,{
      duration: 3000,
    });
  }

  OnChange(inputParam:HTMLInputElement){
    if(inputParam.files.length!=0){
    this.formdata.set(inputParam.getAttribute("name"),inputParam.files[0]); 
  }
  }
  isUploadTrue():boolean{
    this.formcount=0;
    this.formdata.forEach(element=>{
      this.formcount++;
      // console.log(this.formcount);
    })
  
    if((this.formcount-1)==maxproductexcelfiles){
     
  this.isSelected=true;
    }
    return this.isSelected;
  }
  
  
  uploadExcel(){
  console.log(this.formdata.get('categories'),this.formdata.get('subcategories'));

    this.excelUpload.exceluploader(this.formdata).subscribe((res)=>{
        if(res.isPushed==true){
          console.log(res);
          this.openSnackBar();
        }},
    
    err=>{
        alert(err);
    
    })}
}
