import {Component, Input,OnInit} from '@angular/core';
import { cartProduct } from 'src/app/globalmodel/cartProduct';
import { cartDataService } from '../../moduleServices/cartDataService';
import { AddDelService } from '../../../sharedModule/sharedcomponent/addDeleteOptions/services/addelCrud';
import { productCardDTO } from '../../moduleServices/productCartDto';

@Component({
    selector:'cartItem',
    templateUrl:'./cartItem.component.html',
    styleUrls:['./cartItem.component.css'],
    providers:[AddDelService]
})

export class CartItemComponent implements OnInit{
    @Input() cartItem:cartProduct;
    private quantityindex:number;
private totaldiscount:number=0;

constructor(private cartDataService:AddDelService,private productDto:productCardDTO){

}

    ngOnInit(){
this.totaldiscount=parseFloat(((this.cartItem.CostPr-this.cartItem.sellPr)*this.cartItem.Qua).toFixed(2));
    }

    removeFromCart(){
        var obj={
            cartProductId:this.cartItem.CartPId
        }
        this.cartDataService.deleteCartProduct(obj).subscribe(data=>{
            if(data.isDeleted==true){
                this.productDto.setCartStatus(true);
            }
        },err=>{
            alert(err.message);
        })
    }

    sendtoCart(index){
        this.cartItem.Qua=this.cartItem.Qua+index;
        this.cartItem.SubT=this.cartItem.Qua*this.cartItem.sellPr;
        if(index==1){
        this.cartDataService.cartCrud({ 'crud':'add' ,'cartProduct':this.cartItem}).subscribe(data=>{
         if(data.isPushed==true){
        this.productDto.setCartStatus(true);
        }})}
        if(index==-1){
         this.cartDataService.cartCrud({ 'crud':'del' ,'cartProduct':this.cartItem}).subscribe(data=>{
        if(data.isDeleted==true){
            this.productDto.setCartStatus(true);
    
        }})}
}

    
}